import React, {PropTypes} from 'react';
import {connect} from 'react-redux';
import AuctionList, {getAuctionList} from './AuctionList';
import AuctionsFilterDesktop from 'calendar-filters/AuctionsFilterDesktop';
import CategoryFilter from '../calendar-filters/CategoryFilter';
import CategoryTeaser from '../calendar-teaser/CategoryTeaser';
import AuctionsFilterMobile from 'calendar-filters/AuctionsFilterMobile';
import CalendarNavigationComponent from 'calendar-filters/CalendarNavigationComponent';

import './calendar.scss';

export class CalendarComponent extends React.Component {

  render() {
    const {
      extras,
      allBusinessEntitiesText,
      auctionIsLiveText,
      categories,
      allCategoriesText,
      filterByCategoryText,
      hideFilters,
      filterAuctionsMobileText,
      allCategoriesMobileText,
      businessEntities,
      closeFiltersMobileText,
      noAuctionsForFilter,
      linkedCategoryItem,
      hideCategoryFilters,
      auctionTypes,
      disableAuctionTypeFilter,
      disableLocationFilter,
      filterAuctionsDesktopText,
      isResult
    } = this.props;

    if (linkedCategoryItem) {
      return this.renderCategoryPagesGrid(this.props);
    }

    return (
      <div className="calendar-component section-wrapper px1">
        <h1 className="calendar-component__title-main">{this.props.upcomingAuctions}</h1>
        <div className="calendar-component__category-filter-desktop">
          <CategoryFilter
            categories={categories}
            allCategoriesText={allCategoriesText}
            filterByCategoryText={filterByCategoryText}
            hideFilters={hideFilters}
            hideCategoryFilters={hideCategoryFilters}
            isResult={isResult}
            extras={extras} />
        </div>
        <div className="calendar-component__header col col-12">
          <div>
            <AuctionsFilterMobile
              filterAuctionsText={filterAuctionsMobileText}
              categories={categories}
              businessEntities={businessEntities}
              allCategoriesText={allCategoriesMobileText}
              filterByCategoryText={filterByCategoryText}
              allBusinessEntitiesText={allBusinessEntitiesText}
              closeFiltersMobileText={closeFiltersMobileText}
              changeHandler={this.businessEntityChange}
              hideCategoryFilters={hideCategoryFilters}
              auctionTypes={auctionTypes}
              disableAuctionTypeFilter={disableAuctionTypeFilter}
              disableLocationFilter={disableLocationFilter}
              extras={extras} />
          </div>

          <div className="calendar-component__location-filter">
            <AuctionsFilterDesktop
              locationText={filterAuctionsDesktopText}
              businessEntities={businessEntities}
              allBusinessEntitiesText={allBusinessEntitiesText}
              auctionTypes={auctionTypes}
              disableAuctionTypeFilter={disableAuctionTypeFilter}
              disableLocationFilter={disableLocationFilter}
            />
          </div>

          {!extras.isMobile ? <CalendarNavigationComponent {...this.props} isTop isResult={isResult} /> : null}
        </div>
        <AuctionList
          businessEntities={businessEntities}
          categories={categories}
          labels={{auctionIsLiveText}}
          noAuctionsForFilter={noAuctionsForFilter}
          dailyAuctionsTooltip={this.props.dailyAuctionsTooltip}
          classicAuctionsTooltip={this.props.classicAuctionsTooltip}
          adBlocks={this.props.adBlocks}
          extras={extras}
          isResult={isResult} />

        {isResult || extras.isMobile ? <CalendarNavigationComponent {...this.props} /> : null}

        {isResult ? null : <CategoryTeaser {...this.props} />}
      </div>

    );
  }

  /**
   * Render calendar grid as a separate module.
   * @param {object} props react props
   * @returns {object}
   */
  renderCategoryPagesGrid(props) {
    const categoryFilters = {
      allCategories: false,
      filters: {
        [props.linkedCategoryItem.id]: true
      }
    };

    const obj = {
      filters: categoryFilters,
      ...props,
      labels: {auctionIsLiveText: props.auctionIsLiveText},
      auctionTypeFilters: {
        allTypes: true
      }
    };

    const auctionList = getAuctionList(obj);
    if (!auctionList.length) return null;

    return (
      <div className="calendar-component section-wrapper px1">
        <div className="calendar-component__header">
          <h2 className="calendar-component__title">{props.upcomingAuctions}</h2>
        </div>
        <AuctionList {...obj} />
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    auctions: state.modules.data.calendar,
    isResult: state.modules.data.isResult,
    resultAuctions: state.modules.data.resultAuctions,
    linkedCategoryItem: state.page.linkedCategoryItem
  };
}

CalendarComponent.propTypes = {
  auctions: PropTypes.array,
  isResult: PropTypes.bool,
  upcomingAuctions: PropTypes.string,
  auctionIsLiveText: PropTypes.string,
  allBusinessEntitiesText: PropTypes.string,
  extras: PropTypes.object,
  noAuctionsForFilter: PropTypes.string,
  categories: PropTypes.array,
  allCategoriesText: PropTypes.string,
  filterByCategoryText: PropTypes.string,
  hideFilters: PropTypes.bool,
  hideCategoryFilters: PropTypes.bool,
  filterAuctionsMobileText: PropTypes.string,
  filterAuctionsDesktopText: PropTypes.string,
  allCategoriesMobileText: PropTypes.string,
  businessEntities: PropTypes.array,
  closeFiltersMobileText: PropTypes.string,
  linkedCategoryItem: PropTypes.object,
  auctionTypes: PropTypes.object,
  dailyAuctionsTooltip: PropTypes.string,
  classicAuctionsTooltip: PropTypes.string,
  showResultsText: PropTypes.string,
  disableAuctionTypeFilter: PropTypes.bool,
  disableLocationFilter: PropTypes.bool,
  adBlocks: PropTypes.array
};

CalendarComponent.defaultProps = {
  auctions: [],
  upcomingAuctions: '',
  allBusinessEntitiesText: '',
  noAuctionsForFilter: '',
  categories: [],
  allCategoriesText: '',
  allCategoriesMobileText: '',
  filterByCategoryText: '',
  hideFilters: false,
  categoryFiltersActions: {},
  locationFiltersActions: {},
  categoryFilters: {},
  locationFilters: {},
  businessEntities: ['BER', 'NYC'],
  filterAuctionsMobileText: '',
  closeFiltersMobileText: '',
  adBlocks: []
};

export default connect(mapStateToProps, null)(CalendarComponent);



