import React, {PropTypes} from 'react';
import {connect} from 'react-redux';
import LocationFilter from 'calendar-filters/LocationFilter';
import CategoryFilter from 'calendar-filters/CategoryFilter';
import './auctionsFilterMobile.scss';
import { classes } from 'helpers/utils';
import _ from 'helpers/lodash';
import { isMobile } from 'helpers/device-detect';
import AuctionTypeFilter from 'calendar-filters/AuctionTypeFilter';

export class AuctionsFilterMobile extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      sectionsExpanded: false
    };
  }

  componentDidMount() {
    if (!isMobile()) {
      document.addEventListener('touchstart', this.collapseIfClickedOutside);
      document.addEventListener('click', this.collapseIfClickedOutside);
    }
  }

  componentWillUnmount() {
    document.removeEventListener('touchstart', this.collapseIfClickedOutside);
    document.removeEventListener('click', this.collapseIfClickedOutside);
  }

  collapseIfClickedOutside = (e) => {
    const className = _.get(e, 'target.parentNode.className', '');

    if (className.indexOf('auctions-filters-element') === -1 &&
      className.indexOf('auction-type-filter') === -1 &&
      className.indexOf('location-filter') === -1 &&
      className.indexOf('category-filter') === -1 &&
      className.indexOf('single-category') === -1) {

      this.setState({sectionsExpanded: false});
    }
  }

  change = () => {
    this.setState({sectionsExpanded: !this.state.sectionsExpanded});
  };

  closeAndScrollUp = () => {
    if (isMobile()) {
      window.scrollBy(0, -(this.refs.element.offsetHeight));
    }
    this.change();
  }

  render() {
    const {
      filterAuctionsText,
      allCategoriesText,
      categories,
      businessEntities,
      allBusinessEntitiesText,
      closeFiltersMobileText,
      locationFilters,
      categoryFilters,
      hideCategoryFilters,
      auctionTypes,
      auctionTypeFilters,
      disableAuctionTypeFilter,
      disableLocationFilter,
      extras
    } = this.props;

    const filtersApplied = !(locationFilters.allLocations && categoryFilters.allCategories && auctionTypeFilters.allTypes);
    const filterIndicator = filtersApplied ? <div className="calendar-component__title-indicator" /> : null;
    const auctionTypeFilter = !disableAuctionTypeFilter ? <AuctionTypeFilter auctionTypes={auctionTypes} /> : null;
    const locationFilter = !disableLocationFilter ? (
      <LocationFilter
        businessEntities={businessEntities}
        allBusinessEntitiesText={allBusinessEntitiesText} />
    ) : null;

    const auctionFilters = this.state.sectionsExpanded ?
      (<div className="auctions-filters"
        ref="element">
        <CategoryFilter
          categories={categories}
          allCategoriesText={allCategoriesText}
          extras={extras}
          hideCategoryFilters={hideCategoryFilters} />
        {auctionTypeFilter}
        {locationFilter}
        <input className="auctions-filters__button" type="button" value={closeFiltersMobileText}
               onClick={this.closeAndScrollUp} />
        <i />
      </div>) : null;

    const titleContainerClasses = classes({
      'calendar-component__title-container py1': true,
      'calendar-component__title-container--collapsed': !this.state.sectionsExpanded,
      'active': this.state.sectionsExpanded
    });

    return (
      <div className="auctions-filters-element section-wrapper px1">
        <div className="auctions-filters-element__item">
          <input type="checkbox" checked={!this.state.sectionsExpanded}
                onChange={this.change} id="mobile_auctions_filter"
                className={this.state.sectionsExpanded ? 'active':''} />
          <i className="py1" />
          <div className="calendar-component__arrow" />
          <div className={titleContainerClasses}>
            <h3 className="calendar-component__title">{filterAuctionsText}</h3>
            {filterIndicator}
          </div>
          {auctionFilters}
        </div>
      </div>);
  }
}

function mapStateToProps(state) {
  return {
    locationFilters: state.locationFilters,
    categoryFilters: state.categoryFilters,
    auctionTypeFilters: state.auctionTypeFilters
  };
}

AuctionsFilterMobile.propTypes = {
  categories: PropTypes.array,
  filterAuctionsText: PropTypes.string,
  allCategoriesText: PropTypes.string,
  businessEntities: PropTypes.array,
  businessEntityFilter: PropTypes.string,
  allBusinessEntitiesText: PropTypes.string,
  closeFiltersMobileText: PropTypes.string,
  changeHandler: PropTypes.func,
  locationFilters: PropTypes.object,
  extras: PropTypes.object,
  categoryFilters: PropTypes.object,
  hideCategoryFilters: PropTypes.bool,
  auctionTypes: PropTypes.object,
  disableAuctionTypeFilter: PropTypes.bool,
  disableLocationFilter: PropTypes.bool,
  auctionTypeFilters: PropTypes.object
};

AuctionsFilterMobile.defaultProps = {
  filterAuctionsText: '',
  allCategoriesText: '',
  closeFiltersMobileText: ''
};

export default connect(mapStateToProps)(AuctionsFilterMobile);



