import React, {PropTypes} from 'react';
import MarkdownElement from 'components/markdown/MarkdownElement';
import {classes} from 'helpers/utils';
import {connect} from 'react-redux';
import _ from 'helpers/lodash';
import { bindActionCreators } from 'redux';
import * as ModuleActions from 'ui/actions/ModuleActions';
import * as CalendarNavigationActions from 'ui/actions/CalendarNavigationActions';
import {getAuctionList} from 'calendar/AuctionList';

export class CalendarNavigationComponent extends React.Component {

  componentDidMount() {
    this.props.navigationActions.initPagination(this.props.extras.isMobile);
  }

  nextPage = () => {
    this.props.navigationActions.updatePagination({
      page: _.get(this.props, 'calendarNavigation.settings.page') + 1
    });
  };

  viewAll = () => {
    const {calendarNavigation, navigationActions} = this.props;

    navigationActions.updatePagination({
      page: (getAuctionList(this.props).totalAuctions / _.get(calendarNavigation, 'settings.pageSize', 1)) + 1
    });
  };

  generateLabel(filterResult, currentLimit) {
    const {showResultsText, showResultsTextMiddle, showResultsTextSmall} = this.props;
    let selectedLabel = showResultsTextSmall;

    if(filterResult.totalAuctions > currentLimit) {
      selectedLabel = showResultsText;
    }
    else if (filterResult.totalAuctions > 1 && filterResult.totalAuctions <= currentLimit || filterResult.totalAuctions === 0) {
      selectedLabel = showResultsTextMiddle;
    }

    return selectedLabel.replace('{currentAuctions}', filterResult.resultAuctions.length)
      .replace('{allAuctions}', filterResult.totalAuctions);
  }

  render() {
    const {isTop, extras, calendarNavigation, isResult, viewAll, viewMore} = this.props;
    const currentLimit = _.get(calendarNavigation, 'settings.page', 1) * _.get(calendarNavigation, 'settings.pageSize', 1);
    const filterResult = getAuctionList(this.props);
    const showResultsContent = this.generateLabel(filterResult, currentLimit);

    const componentClasses = classes({
      'calendar-component__navigation-top': isTop && !extras.isMobile,
      'calendar-component__navigation-bottom': !isTop && !extras.isMobile,
      'calendar-component__navigation-mobile': extras.isMobile
    });

    const displayedAmount = _.get(calendarNavigation, 'settings.page', 0) * _.get(calendarNavigation, 'settings.pageSize', 0);

    const buttons = isResult && !isTop && filterResult.totalAuctions > displayedAmount ? (
      <div className="navigation">
        <span className="navigation-button" onClick={this.nextPage}>{viewMore}</span>
        <span className="navigation-delimiter"> | </span>
        <span className="navigation-button" onClick={this.viewAll}>{viewAll}</span>
      </div>
    ) : null;
    
    if (!extras.isMobile && !isTop && filterResult.totalAuctions <= currentLimit) {
      return null;
    }

    return  (
      <div className={componentClasses}>
        <span>
          <MarkdownElement text={showResultsContent} />
        </span>
        {buttons}
      </div>
    );
  };
}

function mapStateToProps(state) {
  return {
    config: state.config,
    modules: state.modules,
    categoryFilters: state.categoryFilters,
    locationFilters: state.locationFilters,
    calendarNavigation: state.calendarNavigation,
    auctionTypeFilters: state.auctionTypeFilters,
    data: state.data
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(ModuleActions, dispatch),
    navigationActions: bindActionCreators(CalendarNavigationActions, dispatch)
  };
}

CalendarNavigationComponent.propTypes = {
  showResultsText: PropTypes.string,
  showResultsTextMiddle: PropTypes.string,
  showResultsTextSmall: PropTypes.string,
  viewAll: PropTypes.string,
  viewMore: PropTypes.string,
  isTop: PropTypes.bool,
  isResult: PropTypes.bool,
  extras: PropTypes.object,
  auctions: PropTypes.array,
  modules: PropTypes.object,
  actions: PropTypes.object,
  categoryFilters: PropTypes.object,
  locationFilters: PropTypes.object,
  navigationActions: PropTypes.object,
  calendarNavigation: PropTypes.object,
  auctionTypeFilters: PropTypes.object,
  config: PropTypes.object
};

CalendarNavigationComponent.defaultProps = {
  showResultsText: ''
};

export default connect(mapStateToProps, mapDispatchToProps)(CalendarNavigationComponent);



