import React, {PropTypes} from 'react';
import {connect} from 'react-redux';
import LocationFilter from 'calendar-filters/LocationFilter';
import AuctionTypeFilter from 'calendar-filters/AuctionTypeFilter';
import { isBrowser, classes } from 'helpers/utils';
import _ from 'helpers/lodash';
import './auctionFilterDesktop.scss';

export class AuctionsFilterDesktop extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      sectionsExpanded: false
    };
  }

  componentDidMount() {
    this._isMounted = true;

    if (isBrowser()) {
      document.addEventListener('touchstart', this.collapseIfClickedOutside.bind(this));
      document.addEventListener('click', this.collapseIfClickedOutside.bind(this));
    }
  }

  componentWillUnmount() {
    this._isMounted = false;

    if (isBrowser()) {
      document.removeEventListener('touchstart', this.collapseIfClickedOutside.bind(this));
      document.removeEventListener('click', this.collapseIfClickedOutside.bind(this));
    }
  }

  collapseIfClickedOutside(e) {
    if (!this._isMounted) return;

    const className = _.get(e, 'target.parentNode.className', '');

    if (className.indexOf('auction-filters-desktop') === -1 &&
      className.indexOf('auction-type-filter') === -1 &&
      className.indexOf('location-filter') === -1) {

      this.setState({sectionsExpanded: false});
    }
  }

  change = () => {
    this.setState({sectionsExpanded: !this.state.sectionsExpanded});
  }

  render() {
    const {
      locationText,
      businessEntities,
      allBusinessEntitiesText,
      locationFilters,
      auctionTypes,
      auctionTypeFilters,
      disableAuctionTypeFilter,
      disableLocationFilter
    } = this.props;

    const filtersApplied = !(locationFilters.allLocations && auctionTypeFilters.allTypes);
    const filterIndicator = filtersApplied ? <div className="calendar-component__title-indicator" /> : null;
    const auctionTypeFilter = !disableAuctionTypeFilter ? <AuctionTypeFilter auctionTypes={auctionTypes} /> : null;
    const locationFilter = !disableLocationFilter ?
      <LocationFilter
        businessEntities={businessEntities}
        allBusinessEntitiesText={allBusinessEntitiesText} /> : null;

    const titleClasses = classes({
      'calendar-component__title-container': true,
      'active': this.state.sectionsExpanded
    });

    const filterContainer = disableAuctionTypeFilter && disableLocationFilter ? null : (
      <div className="auction-filters-desktop-element__item">
        <input type="checkbox" checked={!this.state.sectionsExpanded}
               onChange={this.change} id="auction-type-filter" className={this.state.sectionsExpanded ? 'active':''} />
        <div className={titleClasses}>
          <h3 className="calendar-component__title">{locationText}</h3>
          {filterIndicator}
        </div>
        <i />
        <div className="calendar-component__arrow" />
        <div className="auction-filters-desktop">
          {auctionTypeFilter}
          {locationFilter}
        </div>
      </div>
    );
    
    return (
      <div className="auction-filters-desktop-element section-wrapper">
        {filterContainer}
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    locationFilters: state.locationFilters,
    auctionTypeFilters: state.auctionTypeFilters
  };
}

AuctionsFilterDesktop.propTypes = {
  businessEntities: PropTypes.array,
  allBusinessEntitiesText: PropTypes.string,
  locationText: PropTypes.string,
  locationFilters: PropTypes.object,
  auctionTypes: PropTypes.object,
  disableAuctionTypeFilter: PropTypes.bool,
  disableLocationFilter: PropTypes.bool,
  auctionTypeFilters: PropTypes.object
};

export default connect(mapStateToProps)(AuctionsFilterDesktop);



