import React, {PropTypes} from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import {classes} from 'helpers/utils';
import * as categoryFiltersActions from 'ui/actions/categoryFiltersActions';
import * as calendarNavigationActions from 'ui/actions/CalendarNavigationActions';
import 'components/field/style.scss';
import './categoryFilter.scss';
import _ from 'helpers/lodash';
import gtm from 'helpers/gtm';

export class CategoryFilter extends React.Component {
  render() {
    const {
      filterByCategoryText,
      categories,
      categoryFilters,
      allCategoriesText,
      extras,
      hideCategoryFilters
    } = this.props;

    if (hideCategoryFilters) return null;

    const isMobile = extras.isMobile;

    const filters = categories.map(f => {
      let {title} = f.content;
      return this.renderCategoryFilter(categoryFilters.filters[f.id] || false, title, f.id);
    });

    const listClasses = classes({
      'category-filter__list': true,
      'col col-12': !isMobile
    });

    const allCategoriesCheckbox = this.renderAllCategoriesCheckBox(categoryFilters.allCategories, allCategoriesText, 'allCategories');
    return (
      <div className="category-filter">
        <div className="category-filter__title">{filterByCategoryText}</div>
        {allCategoriesCheckbox}
        <ul className={listClasses}>
          {filters}
        </ul>
      </div>);
  }

  renderCategoryFilter(checked, label, id) {
    const props = {
      checked,
      value: checked,
      id: `${id}`
    };

    const isMobile = _.get(this.props, 'extras.isMobile');
    const checkClass = classes({'checked': checked, 'checkbox-check': true});
    const itemClasses = classes({
      'category-filter__list-item checkbox-wrapper': true,
      'col col-4': !isMobile
    });

    return (
      <li className={itemClasses} key={props.id}>
            <span className="checkbox single-category">
              <span className={checkClass} />
              <input className="category-filter__list-item-input" type="checkbox" {...props}
                     onChange={this.handleChange} />
            </span>
        <label className="category-filter__list-item-label" htmlFor={props.id}>{label}</label>
      </li>
    );
  }

  renderAllCategoriesCheckBox(checked, label, id) {
    const props = {
      checked,
      value: checked,
      id: `${id}`
    };
    const checkClass = classes({'checked': checked, 'checkbox-check': true});
    return (
      <div className="category-filter__all-categories checkbox-wrapper" key={props.id}>
        <div className="checkbox category-filter__all-categories-checkbox">
          <div className={checkClass} />
          <input type="checkbox" {...props} onChange={this.handleAllCategoriesChange} />
        </div>
        <label htmlFor={props.id}>{label}</label>
      </div>
    );
  }

  handleAllCategoriesChange = (e) => {
    if (!e.target.checked) return e.preventDefault();
    this.sendTrackingEvent(this.props.allCategoryTrackingId);
    this.props.actions.allCategoriesChange();
  };

  handleChange = (e) => {
    this.sendTrackingEvent(e.target.id);
    this.props.actions.changeFilterState({id: e.target.id});
  };

  sendTrackingEvent(id) {
    gtm.event(`category-filter-${id}`);
  }
}

CategoryFilter.propTypes = {
  categories: PropTypes.arrayOf(PropTypes.object),
  allCategoriesText: PropTypes.string,
  filterByCategoryText: PropTypes.string,
  hideFilters: PropTypes.bool,
  hideCategoryFilters: PropTypes.bool,
  actions: PropTypes.object,
  extras: PropTypes.object,
  categoryFilters: PropTypes.object,
  allCategoryTrackingId: PropTypes.string
};

CategoryFilter.defaultProps = {
  actions: {},
  extras: {},
  categories: [],
  allCategoriesText: '',
  filterByCategoryText: '',
  hideFilters: false,
  hideCategoryFilters: false,
  categoryFilters: {},
  allCategoryTrackingId: 'ALL'
};

function mapStateToProps(state) {
  return {
    categoryFilters: state.categoryFilters
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(categoryFiltersActions, dispatch),
    navigationActions: bindActionCreators(calendarNavigationActions, dispatch)
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(CategoryFilter);



