import React, {PropTypes} from 'react';
import {connect} from 'react-redux';
import _ from 'helpers/lodash';
import {classes} from 'helpers/utils';
import {bindActionCreators} from 'redux';
import * as auctionTypeFiltersActions from 'ui/actions/AuctionTypeFiltersActions';
import gtm from 'helpers/gtm';
import './auctionTypeFilter.scss';

export class AuctionTypeFilter extends React.Component {

  render() {
    const {
      auctionTypes,
      auctionTypeFilters
    } = this.props;

    const auctionTypesData = _.get(auctionTypes, 'content.dataBag');

    const items = auctionTypesData.items.map((item, index) => {
      return this.renderItemCheckbox(auctionTypeFilters.filters[item.key], auctionTypesData.items[index], index);
    });

    const allTypesCheckbox = this.renderAllTypesCheckBox(auctionTypeFilters.allTypes, auctionTypesData.header);

    return (
      <div className="auction-type-filter__wrapper">
        {allTypesCheckbox}
        <div className="auction-type-filter__other-items">
          {items}
        </div>
      </div>
    );
  }

  renderAllTypesCheckBox = (checked, data) => {
    const checkClass = classes({'checked': checked, 'checkbox-check': true});

    return (
      <div className="auction-type-filter__all-item checkbox-wrapper">
        <div className="auction-type-filter__checkbox checkbox">
          <div className={checkClass} />
          <input type="checkbox" id={data.key} onChange={this.handleAllTypesChange} />
        </div>
        <label htmlFor={data.key}>{data.label}</label>
      </div>
    );
  };

  renderItemCheckbox = (checked, data, index) => {
    const checkClass = classes({'checked': checked, 'checkbox-check': true});

    return (
      <li className="auction-type-filter__item checkbox-wrapper" key={index}>
        <div className="auction-type-filter__checkbox checkbox single-category">
          <div className={checkClass} />
          <input type="checkbox" id={data.key} onChange={this.handleChange} />
        </div>
        <label htmlFor={data.key}>{data.label}</label>
      </li>);
  };

  handleAllTypesChange = () => {
    const {allTypesTrackingId, actions} = this.props;
    this.sendTrackingEvent(allTypesTrackingId);
    actions.allTypesChange();
  };

  handleChange = (e) => {
    const { id } = e.target;
    this.sendTrackingEvent(id);
    this.props.actions.typeFilterChange({
      id: id
    });
  };

  sendTrackingEvent(id) {
    gtm.event(`auctiontype-filter-${id}`);
  }
}

function mapStateToProps(state) {
  return {
    auctionTypeFilters: state.auctionTypeFilters
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(auctionTypeFiltersActions, dispatch)
  };
}

AuctionTypeFilter.propTypes = {
  actions: PropTypes.object,
  auctionTypes: PropTypes.object,
  auctionTypeFilters: PropTypes.object,
  allTypesTrackingId: PropTypes.string
};

AuctionTypeFilter.defaultProps = {
  allTypesTrackingId: 'ALL'
};

export default connect(mapStateToProps, mapDispatchToProps)(AuctionTypeFilter);



