import React, {PropTypes} from 'react';
import EntryFactory from 'ui/EntryFactory';

import AuctionItem from './AuctionItem';
import AuctionListData from './AuctionListData';

import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import _ from 'helpers/lodash';
import * as categoryFiltersActions from 'ui/actions/categoryFiltersActions';
import * as locationFiltersActions from 'ui/actions/locationFiltersActions';


export class AuctionList extends React.Component {

  componentWillMount() {
    const {
      categoryFiltersActions: categoryActions,
      locationFiltersActions: locationActions,
      categories,
      businessEntities,
      categoryFilters,
      locationFilters
    } = this.props;

    //Initialize stores with false keys
    let filters = categoryFilters.filters || {};
    categories.forEach(c => {
      if (!filters[c.id]) filters[c.id] = false;
    });

    categoryActions.setInitial({
      ...categoryFilters,
      filters
    });

    let businessFilters = locationFilters.filters || {};
    businessEntities.forEach(c => {
      if (!businessFilters[c.id]) businessFilters[c.id] = false;
    });

    locationActions.setInitial({
      ...locationFilters,
      filters: businessFilters
    });
  }

  render() {
    let auctionsList = getAuctionList(this.props).resultAuctions;

    if (!auctionsList) return null;
    const isMobile = this.props.extras.isMobile;

    if (auctionsList.length) {
      const items = new AuctionListData(auctionsList, this.props.adBlocks).map((data, index) => {
        if (!data || isMobile && data.content_type) return;

        if (data.content_type) {
          return (
            <li key={index} className="calendar-component__auction-ad-block">
              <EntryFactory module={data} extras={{...this.props.extras, noUpdate: true}} />
            </li>
          );
        }

        return <AuctionItem auction={data} key={index} {...this.props} />;
      });

      return <ul className="vcalendar calendar-component__auction-list">{items}</ul>;
    }

    return (
      <div className="calendar-component__no-auction">
        <p>{this.props.noAuctionsForFilter}</p>
      </div>
    );
  }

  componentWillUnmount() {
    this.props.categoryFiltersActions.clearState();
    this.props.locationFiltersActions.clearState();
  }

}

/**
 * Return a list of auctions with applied filter.
 * @param {object} props react props
 * @returns {Array}
 */
export function getAuctionList(props) {
  const {
    auctions,
    locationFilters,
    categories,
    auctionTypeFilters,
    linkedCategoryItem,
    filters,
    calendarNavigation,
    isResult
  } = props;

  let categoryFilters = props.categoryFilters;
  let auctionTypeFilter = _.get(linkedCategoryItem, 'content.mapping.auctionTypeFilters', auctionTypeFilters);
  let auctionsList = auctions;

  if (filters) {
    categoryFilters = filters;
    locationFilters.allLocations = true;
  }

  if (!locationFilters.allLocations) {
    const selectedLocation = _.first(_.keys(locationFilters.filters).filter(f => locationFilters.filters[f]));
    auctionsList = _.filter(auctions, 'businessEntity', selectedLocation);
  }

  if (!categoryFilters.allCategories) {
    let topCategories = categories
      .filter(c => categoryFilters.filters[c.id])
      .map(c => c.content.mapping.categoryIds)
      .reduce((acc, value) => {
        acc = acc.concat(value);
        return acc;
      }, []);

    auctionsList = auctionsList.filter(a => _.intersection(a.topCategories, topCategories).length);
  }

  if (!auctionTypeFilter.allTypes) {
    const selectedType = _.first(_.keys(auctionTypeFilter.filters).filter(f => auctionTypeFilter.filters[f]));
    auctionsList = _.filter(auctionsList, 'auctionType', selectedType);
  }

  const totalAuctions = auctionsList.length;

  if (calendarNavigation.settings && isResult) {
    auctionsList = auctionsList.slice(0, calendarNavigation.settings.page * calendarNavigation.settings.pageSize);
  }

  return {
    resultAuctions: auctionsList,
    totalAuctions
  };
}

function mapStateToProps(state) {
  return {
    auctions: state.modules.data.calendar,
    isResult: state.modules.data.isResult,
    categoryFilters: state.categoryFilters,
    locationFilters: state.locationFilters,
    auctionTypeFilters: state.auctionTypeFilters,
    calendarNavigation: state.calendarNavigation
  };
}

function mapDispatchToProps(dispatch) {
  return {
    categoryFiltersActions: bindActionCreators(categoryFiltersActions, dispatch),
    locationFiltersActions: bindActionCreators(locationFiltersActions, dispatch)
  };
}

AuctionList.propTypes = {
  adBlocks: PropTypes.array,
  businessEntities: PropTypes.array,
  categoryFiltersActions: PropTypes.object,
  locationFiltersActions: PropTypes.object,
  dailyAuctionsTooltip: PropTypes.string,
  classicAuctionsTooltip: PropTypes.string,
  categories: PropTypes.array,
  auctions: PropTypes.array,
  categoryFilters: PropTypes.object,
  locationFilters: PropTypes.object,
  labels: PropTypes.object,
  noAuctionsForFilter: PropTypes.string,
  extras: PropTypes.object,
  filters: PropTypes.object,
  isResult: PropTypes.bool
};

AuctionList.defaultProps = {
  auctions: [],
  adBlocks: []
};

export default connect(mapStateToProps, mapDispatchToProps)(AuctionList);



