import React, {PropTypes} from 'react';
import {connect} from 'react-redux';
import HCalendar from '../hcalendar/HCalendar';
import LinkComp from '../components/linkComp';
import ImageElement from 'components/images/ImageElement';
import {dateToString, setImageSize, buildAuctionLink, classes} from '../helpers/utils';

const NO_IMAGE = require('../../public/icons/NO_IMAGE.svg');

const HIGHLIGHT_IMAGE_SIZE = {
  width: 360,
  height: 270
};

const REGULAR_IMAGE_SIZE = {
  width: 220,
  height: 165
};

const THUMB_IMAGE_SIZE = {
  width: 80,
  height: 60
};

/**
 * Render auction.
 * @returns {object}
 */
class AuctionItem extends React.Component {
  constructor(props) {
    super();

    this.setDefaultImage(props);
  }

  render() {
    const extras = this.props.extras;
    const auction = this.props.auction;
    const isResult = this.props.isResult;
    const isMobile = extras.isMobile;

    const date = dateToString(auction.startDate, extras.locale, true);
    const link = buildAuctionLink(auction, extras);
    const url = this.props.config.originLocation + link;

    const hCalendarBlock = <HCalendar dtStart={auction.startDate} location={auction.businessEntity} />;

    const infoBlock = auction.isLive ?
      <div className="calendar-component__auction-is-live">
        {hCalendarBlock}
        {this.props.labels.auctionIsLiveText}
      </div> :
      <p className="calendar-component__auction-date">
        {hCalendarBlock}
        {date}
      </p>;
    const idBlock = this.renderId(auction);
    const titleBlock = !isResult ? this.renderTitle(auction, isMobile) : this.renderResultsTitle(auction, extras);

    const thumbsBlock = auction.isHighlighted && !isMobile && !isResult ? this.renderThumbs(auction.highlightImages) : null;
    const classNames = classes({
      'calendar-component__auction-item': true,
      vevent: true,
      highlight: auction.isHighlighted && !isResult
    });

    const details = (
      <div className="calendar-component__auction-item-details">
        {!auction.isHighlighted && !isMobile && !isResult ? idBlock : '' }
        {isMobile ? '' : titleBlock}
        {infoBlock}
        <div className="calendar-component__teaser-text description">{auction.teaserText}</div>
      </div>
    );

    const detailsResultPage = (
      <div className="calendar-component__auction-item-details">
        {isMobile ? '' : this.renderResultsTitle(auction, extras)}
        {this.renderTitle(auction, isMobile, isResult)}
      </div>
    );

    return (
      <li className={classNames}>
        <LinkComp className={`calendar-component__auction-link-wrapper url ${isResult ? 'result' : ''}`} spaLink={url} extras={extras}>
          {isMobile ? titleBlock : ''}
          <div className="calendar-component__auction-item-image-wrapper">
            <div className="calendar-component__auction-item-image">
              <ImageElement
                imageUrl={this.state.currentImage}
                title={auction.title} extras={this.props.extras} />
            </div>

            <div className="calendar-component__auction-item-actions">
              {auction.isHighlighted && !isMobile && !isResult ? idBlock : ''}
              {thumbsBlock}
            </div>
          </div>

          {isResult ? detailsResultPage : details}
        </LinkComp>
      </li>
    );
  }

  /**
   * @override
   */
  componentWillReceiveProps(nextProps) {
    this.setDefaultImage(nextProps);
  }

  /**
   * Set default image for auction.
   * @param {object} props
   */
  setDefaultImage(props) {
    const { auction, isResult } = props;
    const imageSize = auction.isHighlighted && !props.isResult ? HIGHLIGHT_IMAGE_SIZE : REGULAR_IMAGE_SIZE;
    const auctionImage = isResult ? auction.image : auction.calendarImage;

    this.defaultImage = auctionImage;

    this.state = {
      currentImage: auctionImage ? setImageSize(imageSize.height, imageSize.width, auctionImage) :
        NO_IMAGE
    };
  }

  /**
   * Render title header.
   * @param {object} auction auction data.
   * @param {boolean} isMobile is device mobile
   * @returns {object}
   */
  renderTitle(auction, isMobile, isResult) {
    if (isMobile && !isResult) {
      return (
        <div className="calendar-component__auction-item-title">
          <h4>
            {this.renderId(auction)}
            <span className="summary">{auction.title}</span>
          </h4>
        </div>
      );
    }
    return (
      <div className="calendar-component__auction-item-title">
        <h4>
          <span className="summary">{auction.title}</span>
        </h4>
      </div>
    );
  }

  renderResultsTitle(auction, extras) {
    const date = dateToString(auction.startDate, extras.locale, false, true);
    return (
      <div className="calendar-component__auction-result-item-title">
          <div>
            <span>{auction.externalNumber}</span>
            <span> | </span>
            <span className="summary">{date}</span>
          </div>
      </div>
    );
  }

  /**
   * Render auction number with icon.
   * @param {object} auction current auction data
   * @returns {object}
   */
  renderId(auction) {
    const type = auction.auctionType;
    const tooltip = type === 'daily' ? 'dailyAuctionsTooltip' : 'classicAuctionsTooltip';

    return (
      <div className={`calendar-component__auction-item-id calendar-component__auction-item-id-${type} uid`}
           title={this.props[tooltip]}>
        {auction.externalNumber}
      </div>
    );
  }

  /**
   * Render thumb section for highlighted auction.
   * @param {Array} images list of images
   * @returns {object}
   */
  renderThumbs(images = []) {
    const changeImage = (value) => {
      this.setState({
        currentImage: setImageSize(HIGHLIGHT_IMAGE_SIZE.height, HIGHLIGHT_IMAGE_SIZE.width,
          value instanceof Object ? this.defaultImage : value)
      });
    };

    const thumbList = images.slice(0, this.props.thumbListLength).map((value, index) => {
      const image = setImageSize(THUMB_IMAGE_SIZE.height, THUMB_IMAGE_SIZE.width, value);
      const handle = changeImage.bind(null, value);

      return (
        <li key={index} onMouseEnter={handle}>
          <ImageElement imageUrl={image} extras={this.props.extras} />
        </li>
      );
    });

    return (
      <ul onMouseLeave={changeImage}>
        {thumbList}
      </ul>
    );
  }
}

function mapStateToProps(state) {
  return {
    config: state.config
  };
}

AuctionItem.propTypes = {
  thumbListLength: PropTypes.number,
  auction: PropTypes.object,
  labels: PropTypes.object,
  extras: PropTypes.object,
  config: PropTypes.object,
  isResult: PropTypes.bool
};

AuctionItem.defaultProps = {
  thumbListLength: 3,
  auction: {
    highlightImages: []
  },
  config: {}
};

export default connect(mapStateToProps, null)(AuctionItem);



