import React, {PropTypes} from 'react';
import EntryFactory from 'ui/EntryFactory';
import {connect} from 'react-redux';
import _ from 'helpers/lodash';

import './categoryTeaser.scss';

export class CategoryTeaser extends React.Component {
  render() {
    return (
      <div className="calendar-category-teaser__wrapper">
        <ul>
          {this.renderTeasers()}
        </ul>
      </div>
    );
  }

  /**
   * Return a list of id's of current applied filters.
   * @returns {Array}
   */
  getCurrentFilters() {
    const filters = this.props.categoryFilters.filters;
    return Object.keys(filters).filter((key) => filters[key]);
  }

  /**
   * Return an component module object to render category teaser
   * @param {string} id filter category id (from contentful)
   * @returns {object}
   */
  getTeaserForFilterId(id) {
    return _.get(_.find(this.props.categories, 'id', id), 'content.categoryTeaserSection');
  }

  /**
   * Render a list of category teasers. If no category selected, rendered 'All Category' teaser.
   * @returns {object}
   */
  renderTeasers() {
    const currentFilters = this.getCurrentFilters();
    const extras = {
      ...this.props.extras,
      lazyLoading: false
    };

    if (!currentFilters.length) {
      if (!this.props.allCategoriesTeaser.content) return null;

      return (
        <li>
          <EntryFactory module={this.props.allCategoriesTeaser} extras={extras} />
        </li>
      );
    }

    return currentFilters.map((filterId, index) => {
      const teaserModule = this.getTeaserForFilterId(filterId);

      if (!teaserModule) return;

      return (
        <li key={index}>
          <EntryFactory module={teaserModule} extras={extras} />
        </li>
      );
    });
  }
}

function mapStateToProps(state) {
  return {
    categoryFilters: state.categoryFilters
  };
}

CategoryTeaser.propTypes = {
  extras: PropTypes.object,
  categoryFilters: PropTypes.object,
  allCategoriesTeaser: PropTypes.object,
  categories: PropTypes.array
};

CategoryTeaser.defaultProps = {
  categoryFilters: {
    filters: {}
  },
  allCategoriesTeaser: {},
  categories: []
};

export default connect(mapStateToProps, null)(CategoryTeaser);



