import React, {PropTypes} from 'react';
import {connect} from 'react-redux';
import {classes} from 'helpers/utils';
import {bindActionCreators} from 'redux';
import * as locationFiltersActions from 'ui/actions/locationFiltersActions';
import {convertToCity} from 'helpers/utils';
import './locationFilter.scss';
import gtm from 'helpers/gtm';

export class LocationFilter extends React.Component {
  render() {
    const {
      businessEntities,
      allBusinessEntitiesText,
      locationFilters
    } = this.props;

    const filters = businessEntities.map(entity => {
      return this.renderCategoryFilter(locationFilters.filters[entity] || false, convertToCity(entity), entity);
    });

    const allLocationsCheckbox = this.renderAllCategoriesCheckBox(locationFilters.allLocations, allBusinessEntitiesText, 'ALL');

    return (
      <div className="location-filter__wrapper">
        {allLocationsCheckbox}
        <div className="location-filter__other-items">
          {filters}
        </div>
      </div>
    );
  }

  renderCategoryFilter(checked, label, id) {
    const props = {
      checked,
      value: checked,
      id: `${id}`
    };
    const checkClass = classes({'checked': checked, 'checkbox-check': true});
    return (
      <li className="location-filter__item checkbox-wrapper" key={props.id}>
        <div className="location-filter__checkbox checkbox single-category">
          <div className={checkClass} />
          <input type="checkbox" {...props} onChange={this.handleChange} />
        </div>
        <label htmlFor={props.id}>{label}</label>
      </li>
    );
  }

  renderAllCategoriesCheckBox(checked, label, id) {
    const props = {
      checked,
      value: checked,
      id: `${id}`
    };
    const checkClass = classes({'checked': checked, 'checkbox-check': true});
    return (
      <div className="location-filter__all-item checkbox-wrapper" key={props.id}>
        <div className="location-filter__checkbox checkbox">
          <div className={checkClass} />
          <input type="checkbox" {...props} onChange={this.handleAllCategoriesChange} />
        </div>
        <label htmlFor={props.id}>{label}</label>
      </div>
    );
  }

  handleAllCategoriesChange = () => {
    const {allLocationsTrackingId, actions} = this.props;
    this.sendTrackingEvent(allLocationsTrackingId);
    actions.allLocationsChange();
  }

  handleChange = (e) => {
    const {id} = e.target;
    this.sendTrackingEvent(id);
    this.props.actions.locationFilterChange({id});
  }

  sendTrackingEvent(id) {
    gtm.event(`location-filter-${id}`);
  }
}

function mapStateToProps(state) {
  return {
    locationFilters: state.locationFilters
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(locationFiltersActions, dispatch)
  };
}

LocationFilter.propTypes = {
  actions: PropTypes.object,
  businessEntities: PropTypes.array,
  locationFilters: PropTypes.object,
  allBusinessEntitiesText: PropTypes.string,
  allLocationsTrackingId: PropTypes.string
};

LocationFilter.defaultProps = {
  allLocationsTrackingId: 'ALL'
};

export default connect(mapStateToProps, mapDispatchToProps)(LocationFilter);



