const COLUMNS_IN_ROW = 4;
const HIGHLIGHT_AUCTION_COL_LENGTH = 2;
const REGULAR_AUCTION_COL_LENGTH = 1;

/**
 * Return a length of taking columns by auction in a row.
 * @param {boolean} isHighlight is auction highlighted
 */
const getAuctionColumnLength = (isHighlight) => isHighlight ? HIGHLIGHT_AUCTION_COL_LENGTH : REGULAR_AUCTION_COL_LENGTH;

/**
 * Highlight Auction Array class. Calculates it's length based on auction type, e.g.
 * if highlighted auction is pushed, length += 2, if simple - 1.
 * @class
 */
class Row {
  constructor() {
    this.items = [];
    this.length = 0;
  }

  push(auction) {
    this.length += getAuctionColumnLength(auction.isHighlighted);
    this.items.push(auction);
  }
}

/**
 * Highlight Auction Array Manager.
 */
class GridMatrix {
  constructor() {
    this.q = [new Row()];
  }

  /**
   * The same as in Array.
   * @param {object} auction
   */
  push(auction) {
    let tuple = this.findRow(auction);
    tuple.push(auction);
  }

  /**
   * Find an appropriate free spaced row. Otherwise, create a new one.
   * @param {object} auction
   * @returns {Row}
   */
  findRow(auction) {
    const auctionLength = getAuctionColumnLength(auction.isHighlighted);

    let value = this.q[this.q.length - 1];
    return ((value.length + auctionLength) <= COLUMNS_IN_ROW) ? value : this.createNewRow();
  }

  /**
   * Create a new row.
   * @returns {Row}
   */
  createNewRow() {
    var o = new Row();
    this.q.push(o);
    return o;
  }
}

export default class AuctionListData {
  constructor(auctionsList, adBlocks) {
    return this.prepare(auctionsList, adBlocks);
  }

  /**
   * Prepare data for output.
   * You can modify columns in row, add additional properties based on Grid structure.
   * @param {Array} auctionsList list of auctions
   * @param {Array} adBlocks list of ad blocks
   * @returns {Array}
   */
  prepare(auctionsList, adBlocks) {
    const matrix = new GridMatrix();
    const adBlocksCopy = adBlocks.slice();
    auctionsList.forEach((auction) => matrix.push(auction));

    return matrix.q.map((value) => {
      if (value.length < COLUMNS_IN_ROW) value.items.push(adBlocksCopy.shift());
      return value.items;
    }).reduce((prev, cur) => prev.concat(cur));
  }
}



